<template>
  <BaseFormField :title="title" :error="error">
    <input
      v-model="dataValue"
      class="form__input"
      :type="type"
      :name="name"
      :placeholder="placeholder"
  /></BaseFormField>
</template>
<script>
import formFieldMixin from "@/mixins/formFieldMixin.js";

export default {
  props: {
    type: {
      default: "text",
    },
    name: {
      default: "name",
    },
  },
  mixins: [formFieldMixin],
};
</script>